.swiper {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;

  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

.swiper {
  width: 100%;
  height: 300px;
  margin-left: auto;
  margin-right: auto;
}

.swiper-slide {
  height: 450px;
  background-size: cover;
  background-position: center;
}

.mySwiper2 {
  height: 80%;
  width: 100%;
  .swiper-slide img {
    display: block;
    object-fit: cover;
    transform: scale(2);
  }
}

.mySwiper {
  height: 20%;
  box-sizing: border-box;
  padding: 10px 0;
  .swiper-slide img {
    display: block;
    object-fit: cover;
    max-width: 80%;
    max-height: 80px;
  }
  .swiper-slide {
    width: 25%;
    height: 100px;
    opacity: 0.6;
  }
  .swiper-slide-thumb-active {
    opacity: 1;
  }
}
